import React, { useState, useContext,useEffect } from "react";
import readXlsxFile from 'read-excel-file'
import { withRouter } from 'react-router-dom';
// import './home.css';
import Header from '../Header/Header';
import Sidebar from '../Sidebar/Sidebar';

const StudentDetails = (props) => {
    const [studArr, setStudArr] = useState([])
    useEffect(() => {
        const requestOptions = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Access-Control-Allow-Origin": "*"
            }
        };
        fetch('https://asychrondemonew.herokuapp.com/api/students', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log("Get stud resp: ", data);
                setStudArr(data);
            });

    }, []);


    // const handleInputChange = (event) => {
    //     //event.persist();
    //     setInputs(inputs => ({ ...inputs, [event.target.name]: event.target.value }));
    // }


    return (
        <div>
            <Header />
            <div className="dashboard-page-one">
                <Sidebar />
                <div className="dashboard-content-one">
                    <div className="homeWrap"></div>
                    <div className="dashboard-content-one">
                        <div className="card height-auto">
                            <div className="card-body">
                                <div className="heading-layout1">
                                    <div className="item-title">
                                        <h3>All Students Data</h3>
                                    </div>
                                    <div className="dropdown">
                                        <a className="dropdown-toggle" href="#" role="button" data-toggle="dropdown"
                                            aria-expanded="false">...</a>

                                        <div className="dropdown-menu dropdown-menu-right">
                                            <a className="dropdown-item" href="#"><i
                                                className="fas fa-times text-orange-red"></i>Close</a>
                                            <a className="dropdown-item" href="#"><i
                                                className="fas fa-cogs text-dark-pastel-green"></i>Edit</a>
                                            <a className="dropdown-item" href="#"><i
                                                className="fas fa-redo-alt text-orange-peel"></i>Refresh</a>
                                        </div>
                                    </div>
                                </div>
                                <form className="mg-b-20">
                                    <div className="row gutters-8">
                                        <div className="col-3-xxxl col-xl-3 col-lg-3 col-12 form-group">
                                            <input type="text" placeholder="Search by Roll ..." className="form-control" />
                                        </div>
                                        <div className="col-4-xxxl col-xl-4 col-lg-3 col-12 form-group">
                                            <input type="text" placeholder="Search by Name ..." className="form-control" />
                                        </div>
                                        <div className="col-4-xxxl col-xl-3 col-lg-3 col-12 form-group">
                                            <input type="text" placeholder="Search by Class ..." className="form-control" />
                                        </div>
                                        <div className="col-1-xxxl col-xl-2 col-lg-3 col-12 form-group">
                                            <button type="submit" className="fw-btn-fill btn-gradient-yellow">SEARCH</button>
                                        </div>
                                    </div>
                                </form>
                                <div className="table-responsive">
                                    <table className="table display data-table text-nowrap">
                                        <thead>
                                            <tr>
                                                <th>
                                                    <div className="form-check">
                                                        <input type="checkbox" className="form-check-input checkAll" />
                                                        <label className="form-check-label">Roll</label>
                                                    </div>
                                                </th>
                                                <th>Name</th>
                                                <th>Gender</th>
                                                <th>Date Of Birth</th>
                                                <th>Class</th>
                                                <th>Batch</th>
                                            </tr>
                                        </thead>
                                        <tbody>
                                        {studArr.map(function(d, idx){
                                                return (
                                                <tr>
                                                <td>
                                                    <div className="form-check">
                                                        <input type="checkbox" className="form-check-input" />
                                                        <label className="form-check-label">{d.roll}</label>
                                                    </div>
                                                </td>
                                                <td>{d.firstName +" "+ d.middleName + " " +d.lastName}</td>
                                                <td>{d.gender}</td>
                                                <td>{new Date(d.dateOfBirth).toDateString()}</td>
                                                <td>{d.class}</td>
                                                <td>{d.batch}</td>
                                            </tr>
                                                )
                                            })}
                                            

                                        </tbody>
                                    </table>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default withRouter(StudentDetails);