import React, { useState, useContext } from "react";
import readXlsxFile from 'read-excel-file'
import { withRouter } from 'react-router-dom';
// import './home.css';
import Header from '../Header/Header';
import Sidebar from '../Sidebar/Sidebar';


const StudentUpload = (props) => {
    //const { dispatch } = useContext(Store);
    // const [isUpload, setIsUpload] = useState(0);
    const [inputs, setInputs] = useState(
        {
            "firstName": "",
            "middleName": "",
            "lastName": "",
            "gender": "",
            "dateOfBirth": "",
            "roll": "",
            "class": "",
            "fees": "",
            "batch": "",
            "phone": "",
            "email": "",
            "college": ""
        });

    const schema = {
        'First Name': {
            prop: 'firstName',
            type: String
        },
        'Middle Name': {
            prop: 'middleName',
            type: String
        },
        'Last Name': {
            prop: 'lastName',
            type: String
        },
        'Gender': {
            prop: 'gender',
            type: String
        },
        'Date Of Birth': {
            prop: 'dateOfBirth',
            type: Date
        },
        'Roll': {
            prop: 'roll',
            type: String
        },
        'Class': {
            prop: 'class',
            type: String
        },
        'Batch': {
            prop: 'batch',
            type: String
        },
        'Fees': {
            prop: 'fees',
            type: String
        },
        'Email': {
            prop: 'email',
            type: String
        },
        'Phone': {
            prop: 'phone',
            type: String
        },
        'College': {
            prop: 'college',
            type: String
        },
    };

    let studArr = [];
    const handleFileChange = (event) => {
        event.preventDefault();
        readXlsxFile(event.target.files[0], { schema }).then((rows) => {
            console.log('result: ', JSON.stringify(rows));
            studArr = rows.rows;
        })
    };

    const handleUploadBtn = (event) => {
        //setIsUpload(!isUpload);
        props.history.push('/home');
    };

    const handleSubmit = (event) => {
        const requestOptions = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Access-Control-Allow-Origin": "*"
            },
            body: JSON.stringify(studArr)
        };
        fetch('https://asychrondemonew.herokuapp.com/api/students', requestOptions)
            .then(response => response.json())
            .then(data => console.log("post stud resp upload: ", data));
    }

    return (
        <div>
            <Header />
            <div className="dashboard-page-one">
                <Sidebar />
                <div className="dashboard-content-one">
                    <div className="homeWrap"></div>
                    <div className="card height-auto">
                        <div className="card-body">
                            <div className="heading-layout1">
                                <div className="item-title">
                                    <h3>Upload Student Data</h3>
                                </div>
                                <div className="form-group mg-t-8">
                                    <button onClick={handleUploadBtn} type="button" className="btn-fill-lg bg-blue-dark btn-hover-yellow">Add</button>
                                </div>
                            </div>
                            <form className="new-added-form">
                                <div className="row">
                                    <div className="col-xl-12 col-lg-6 col-12 form-group">
                                        <label>Choose Excel File *</label>
                                        <input onChange={handleFileChange} type="file" placeholder="" className="form-control" />
                                    </div>
                                </div>
                                <div className="col-12 form-group mg-t-8">
                                    <button onClick={handleSubmit} type="button" className="btn-fill-lg btn-gradient-yellow btn-hover-bluedark">Upload</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default withRouter(StudentUpload);