import React from "react";
import "./styles1.css";

import { Line } from "react-chartjs-2";

const data = {
  labels: ["Jan", "Feb", "Mar", "Apr", "May", "Jun"],
  borderWidth: 0,
  datasets: [
    {
      label: "Total collection",
      data: [500000, 100000, 300000, 500000, 400000, 200000],
      fill: true,
      backgroundColor: "#ff0000a6",
      borderColor: "#ff0000"
    },
    {
      label: "Fees Collection",
      data: [200000, 50000, 100000, 200000, 500000, 300000],
      fill: true,
      backgroundColor: "#417dfc",
      borderColor: "#417dfc"
    }
  ]
};
 


export default class EarningLineChart  extends React.Component {
    render() {
      return ( <div className="App">

      <Line data={data} 
        //  width={500}
        //  height={300}
         options={{
            title:{
              display:true,
              text:'Earning',
              fontSize:20
            },
              scales: {
                xAxes: [{
                    gridLines: {
                        display:false
                    }
                }],
                yAxes: [{
                    gridLines: {
                        display:true
                    }   
                }]
            },
            legend:{
              display:true,
              position:'bottom',
              labels: {
                fontSize:14,
                // fontColor:'red'
            }
            }
          }}
      />
    </div>);
    }
  }