import React, { useState, useContext } from "react";
import readXlsxFile from 'read-excel-file'
import { withRouter } from 'react-router-dom';
import './home.css';
import Header from '../Header/Header';
import Sidebar from '../Sidebar/Sidebar';
import $ from 'jquery';

const Home = (props) => {
    //const { dispatch } = useContext(Store);
    // const [isUpload, setIsUpload] = useState(0);
    const [inputs, setInputs] = useState(
        {
            "firstName": "",
            "middleName": "",
            "lastName": "",
            "gender": "",
            "dateOfBirth": "",
            "roll": "",
            "class": "",
            "fees": "",
            "batch": "",
            "phone": "",
            "email": "",
            "college": ""
        });

    const handleUploadBtn = (event) => {
        //setIsUpload(!isUpload);
        console.log("Student props: ", props);
        props.history.push('/upload');
    };

    const handleSubmit = (event) => {
        console.log("all data: ", inputs);
        const requestOptions = {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
                "Access-Control-Allow-Origin": "*"
            },
            body: JSON.stringify(inputs)
        };
        fetch('https://asychrondemonew.herokuapp.com/api/students', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log("post stud resp: ", data)
                $(".alert-success").show();
                setTimeout(() => $(".alert-success").hide(),1000);
            });
    }

    const handleInputChange = (event) => {
        //event.persist();
        setInputs(inputs => ({ ...inputs, [event.target.name]: event.target.value }));
    }

    return (
        <div>
            <Header />
            <div className="dashboard-page-one">
                <Sidebar />
                <div className="dashboard-content-one">
                    <div className="homeWrap"></div>
                    <div style={{display: "none"}} class="alert alert-success" role="alert">
                        Student Added Successfully!!
                    </div>
                    <div className="card height-auto">
                        <div className="card-body">
                            <div className="heading-layout1">
                                <div className="item-title">
                                    <h3>Add New Student</h3>
                                </div>
                                <div className="form-group mg-t-8">
                                    <button onClick={handleUploadBtn} type="button" className="btn-fill-lg bg-blue-dark btn-hover-yellow">Upload</button>
                                </div>
                            </div>
                            <form className="new-added-form">
                                <div className="row">
                                    <div className="col-xl-4 col-lg-6 col-12 form-group">
                                        <label>First Name *</label>
                                        <input name="firstName" onChange={handleInputChange} value={inputs.firstName} type="text" placeholder="" className="form-control" />
                                    </div>
                                    <div className="col-xl-4 col-lg-6 col-12 form-group">
                                        <label>Middle Name *</label>
                                        <input name="middleName" onChange={handleInputChange} value={inputs.middleName} type="text" placeholder="" className="form-control" />
                                    </div>
                                    <div className="col-xl-4 col-lg-6 col-12 form-group">
                                        <label>Last Name *</label>
                                        <input name="lastName" onChange={handleInputChange} value={inputs.lastName} type="text" placeholder="" className="form-control" />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-xl-6 col-lg-6 col-12 form-group">
                                        <label>Gender *</label>
                                        <select name="gender" onChange={handleInputChange} value={inputs.gender} className="form-control">
                                            <option value="">Please Select Gender *</option>
                                            <option value="M">Male</option>
                                            <option value="F">Female</option>
                                            <option value="O">Others</option>
                                        </select>
                                    </div>
                                    <div className="col-xl-6 col-lg-6 col-12 form-group">
                                        <label>Date Of Birth *</label>
                                        <input name="dateOfBirth" onChange={handleInputChange} value={inputs.dateOfBirth} type="text" placeholder="dd/mm/yyyy" className="form-control air-datepicker"
                                            data-position='bottom right' />
                                        <i className="far fa-calendar-alt"></i>
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-xl-6 col-lg-6 col-12 form-group">
                                        <label>Phone</label>
                                        <input name="phone" onChange={handleInputChange} value={inputs.phone} type="text" placeholder="" className="form-control" />
                                    </div>
                                    <div className="col-xl-6 col-lg-6 col-12 form-group">
                                        <label>Email</label>
                                        <input name="email" onChange={handleInputChange} value={inputs.email} type="text" placeholder="" className="form-control" />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-xl-6 col-lg-6 col-12 form-group">
                                        <label>Roll</label>
                                        <input name="roll" onChange={handleInputChange} value={inputs.roll} type="text" placeholder="" className="form-control" />
                                    </div>
                                    <div className="col-xl-6 col-lg-6 col-12 form-group">
                                        <label>Fees</label>
                                        <input name="fees" onChange={handleInputChange} value={inputs.fees} type="text" placeholder="" className="form-control" />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-xl-4 col-lg-6 col-12 form-group">
                                        <label>Class *</label>
                                        <select name="class" onChange={handleInputChange} value={inputs.class} className="form-control">
                                            <option value="">Please Select Class *</option>
                                            <option value="Class1">Class1</option>
                                            <option value="Class2">Class2</option>
                                            <option value="Class3">Class3</option>
                                        </select>
                                    </div>

                                    <div className="col-xl-4 col-lg-6 col-12 form-group">
                                        <label>Batch *</label>
                                        <select name="batch" onChange={handleInputChange} value={inputs.batch} className="form-control">
                                            <option value="">Please Select Batch *</option>
                                            <option value="Batch1">Batch1</option>
                                            <option value="Batch2">Batch2</option>
                                            <option value="Batch3">Batch3</option>
                                        </select>
                                    </div>
                                    <div className="col-xl-4 col-lg-6 col-12 form-group">
                                        <label>College</label>
                                        <input name="college" onChange={handleInputChange} value={inputs.college} type="text" placeholder="" className="form-control" />
                                    </div>
                                </div>
                                <div className="col-12 form-group mg-t-8">
                                    <button onClick={handleSubmit} type="button" className="btn-fill-lg btn-gradient-yellow btn-hover-bluedark">Save</button>
                                    <button type="reset" className="btn-fill-lg bg-blue-dark btn-hover-yellow">Reset</button>
                                </div>
                            </form>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default withRouter(Home);