
import React from 'react';
import {Pie, Doughnut} from 'react-chartjs-2';

const state = {
  labels: [ 'Male Students', 'Female Students'],
  datasets: [
    {
      label: 'Students',
      backgroundColor: [
        
        '#ffa601',
        '#304ffe'
       
      ],
      hoverBackgroundColor: [
        '#ff8e01',
      '#305cfe'
   
      ],
      data: [500, 300]
    }
  ]
}

export default class StudentsPieChart extends React.Component {
  render() {
    return (
      <div>
        {/* <Pie
          data={state}
          options={{
            title:{
              display:true,
              text:'Average Rainfall per month',
              fontSize:20
            },
            legend:{
              display:true,
              position:'right'
            }
          }}
        /> */}

        <Doughnut
          data={state}
          // width={500}
          // height={300}
          options={{
            title:{
              display:true,
              text:'Students',
              fontSize:20
            },
            legend:{
              display:true,
              position:'bottom',
              labels: {
                fontSize:14,
                // fontColor:'red'
            }
            }
          }}
        />
      </div>
    );
  }
}