import React from "react";
// import './NavBar.css';
import { BrowserRouter as Router, Route, Switch, withRouter,Link  } from 'react-router-dom';
import $ from 'jquery';

const Sidebar = (props) => {

    const utype = localStorage.getItem("utype");

    $('.sidebar-toggle-view').on('click', '.sidebar-nav-item .nav-link', function (e) {
        if (!$(this).parents('#wrapper').hasClass('sidebar-collapsed')) {
            var animationSpeed = 300,
                subMenuSelector = '.sub-group-menu',
                $this = $(this),
                checkElement = $this.next();
            if (checkElement.is(subMenuSelector) && checkElement.is(':visible')) {
                checkElement.slideUp(animationSpeed, function () {
                    checkElement.removeClass('menu-open');
                });
                checkElement.parent(".sidebar-nav-item").removeClass("active");
            } else if ((checkElement.is(subMenuSelector)) && (!checkElement.is(':visible'))) {
                var parent = $this.parents('ul').first();
                var ul = parent.find('ul:visible').slideUp(animationSpeed);
                ul.removeClass('menu-open');
                var parent_li = $this.parent("li");
                checkElement.slideDown(animationSpeed, function () {
                    checkElement.addClass('menu-open');
                    parent.find('.sidebar-nav-item.active').removeClass('active');
                    parent_li.addClass('active');
                });
            }
            if (checkElement.is(subMenuSelector)) {
                e.preventDefault();
            }
        } else {
            if ($(this).attr('href') === "#") {
                e.preventDefault();
            }
        }
    });

    /*-------------------------------------
        Sidebar Menu Control
      -------------------------------------*/
    $(".sidebar-toggle").on("click", function () {
        window.setTimeout(function () {
            $("#wrapper").toggleClass("sidebar-collapsed");
        }, 500);
    });

    /*-------------------------------------
        Sidebar Menu Control Mobile
      -------------------------------------*/
    $(".sidebar-toggle-mobile").on("click", function () {
        $("#wrapper").toggleClass("sidebar-collapsed-mobile");
        if ($("#wrapper").hasClass("sidebar-collapsed")) {
            $("#wrapper").removeClass("sidebar-collapsed");
        }
    });

    return (
        <div>
            <div style={{ height: "100%" }} className="sidebar-main sidebar-menu-one sidebar-expand-md sidebar-color">
                <div className="mobile-sidebar-header d-md-none">
                    <div className="header-logo">
                        <a href="index.html"><img src={process.env.PUBLIC_URL + '/img/logo1.png'} alt="logo" /></a>
                    </div>
                </div>
                <div className="sidebar-menu-content">
                    <ul className="nav nav-sidebar-menu sidebar-toggle-view">
                        {utype === "parent" || utype === "cadmin"
                            ?
                            <li className="nav-item sidebar-nav-item">
                                <a href="#" className="nav-link"><i className="flaticon-dashboard"></i><span>Dashboard</span></a>
                                <ul className="nav sub-group-menu sub-group-active">
                                    {utype === "cadmin"
                                        ?
                                        <li className="nav-item">
                                            <Link className="nav-link menu-active" to="/admin"><i className="fas fa-angle-right"></i>Admin</Link>
                                            {/* <a href="index.html" className="nav-link menu-active"><i className="fas fa-angle-right"></i>Admin</a> */}
                                        </li>
                                        :
                                        <li className="nav-item">
                                            <Link className="nav-link menu-active" to="/parents"><i className="fas fa-angle-right"></i>Parents</Link>
                                            {/* <a href="index4.html" className="nav-link menu-active"><i className="fas fa-angle-right"></i>Parents</a> */}
                                        </li>
                                    }
                                </ul>
                            </li>
                            :
                            <li className="nav-item sidebar-nav-item">
                                <a href="#" className="nav-link"><i className="flaticon-classmates"></i><span>Students</span></a>
                                <ul className="nav sub-group-menu sub-group-active">
                                    <li className="nav-item">
                                        <Link className="nav-link menu-active" to="/home"><i className="fas fa-angle-right"></i>Admission Form</Link>
                                        {/* <a href="admit-form.html" className="nav-link menu-active"><i className="fas fa-angle-right"></i>Admission Form</a> */}
                                    </li>
                                    <li className="nav-item">
                                    <Link className="nav-link menu-active" to="/student-details"><i className="fas fa-angle-right"></i>Student Details</Link>
                                        {/* <a href="student-promotion.html" className="nav-link"><i className="fas fa-angle-right"></i>Student Details</a> */}
                                    </li>
                                </ul>
                            </li>
                        }
                        {/* <li className="nav-item sidebar-nav-item">
                                <a href="#" className="nav-link"><i className="flaticon-couple"></i><span>Parents</span></a>
                                <ul className="nav sub-group-menu">
                                    <li className="nav-item">
                                        <a href="all-parents.html" className="nav-link"><i className="fas fa-angle-right"></i>All
                                            Parents</a>
                                    </li>
                                    <li className="nav-item">
                                        <a href="parents-details.html" className="nav-link"><i
                                            className="fas fa-angle-right"></i>Parents Details</a>
                                    </li>
                                    <li className="nav-item">
                                        <a href="add-parents.html" className="nav-link"><i className="fas fa-angle-right"></i>Add
                                            Parent</a>
                                    </li>
                                </ul>
                            </li> */}
                    </ul>
                </div>
            </div>
        </div>
    )
};

export default withRouter(Sidebar);