import React, { useState, useContext, useEffect } from "react";
import readXlsxFile from 'read-excel-file'
import { withRouter } from 'react-router-dom';
// import './home.css';
import Header from '../Header/Header';
import Sidebar from '../Sidebar/Sidebar';
import Modal from '../PaymentModal/PaymentModal';


const Parents = (props) => {
    const [showDialog, setShowDialog] = useState(false)
    const [studArr, setStudArr] = useState([]);
    //const { dispatch } = useContext(Store);
    // const [isUpload, setIsUpload] = useState(0);
    // const [inputs, setInputs] = useState(
    //     {
    //         "firstName": "",
    //         "middleName": "",
    //         "lastName": "",
    //         "gender": "",
    //         "dateOfBirth": "",
    //         "roll": "",
    //         "className": ""
    //     });


    // const handleSubmit = (event) => {
    //     console.log("all data: ", inputs);
    // }
    const [inputs, setInputs] = useState(
        {
            "cardNumber": "",
            "cvv": "",
            "expDate": "",
        });

    useEffect(() => {
        const requestOptions = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Access-Control-Allow-Origin": "*"
            }
        };
        fetch('https://asychrondemonew.herokuapp.com/api/students?parentName=D11', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log("Get parent resp: ", data);
                setStudArr(data);
            });

    }, []);

    const handlePayClick = (event) => {
        console.log("pay click");
        setShowDialog(true);
    }
    const handleInputChange = (event) => {
        //event.persist();
        setInputs(inputs => ({ ...inputs, [event.target.name]: event.target.value }));
    }

    return (
        <div>
            <Header />
            <div className="dashboard-page-one">
                <Sidebar />
                <div className="dashboard-content-one">
                    <div className="homeWrap"></div>
                    <div className="row">
                        <div className="col-5-xxxl col-12">
                            <div className="card dashboard-card-twelve">
                                <div className="card-body">
                                    <div className="heading-layout1">
                                        <div className="item-title">
                                            <h3>My Kids</h3>
                                        </div>
                                        <div className="dropdown">
                                            <a className="dropdown-toggle" href="#" role="button" data-toggle="dropdown"
                                                aria-expanded="false">...</a>

                                            <div className="dropdown-menu dropdown-menu-right">
                                                <a className="dropdown-item" href="#"><i
                                                    className="fas fa-times text-orange-red"></i>Close</a>
                                                <a className="dropdown-item" href="#"><i
                                                    className="fas fa-cogs text-dark-pastel-green"></i>Edit</a>
                                                <a className="dropdown-item" href="#"><i
                                                    className="fas fa-redo-alt text-orange-peel"></i>Refresh</a>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="kids-details-wrap">
                                        <div className="row">
                                            {studArr.map(function (d, idx) {
                                                return (
                                                    <div className="col-12-xxxl col-xl-6 col-12">
                                                        <div className="kids-details-box">
                                                            <div className="item-img">
                                                                <img src="img/figure/student1.png" alt="kids" />
                                                            </div>
                                                            <div className="item-content table-responsive">
                                                                <table className="table text-nowrap">
                                                                    <tbody>
                                                                        <tr>
                                                                            <td>Name:</td>
                                                                            <td>{d.firstName +" " +d.lastName}</td>
                                                                        </tr>
                                                                        <tr>
                                                                            <td>Gender:</td>
                                                                            <td>{d.gender}</td>
                                                                        </tr>
                                                                        <tr>
                                                                            <td>Class:</td>
                                                                            <td>{d.class}</td>
                                                                        </tr>
                                                                        <tr>
                                                                            <td>Roll:</td>
                                                                            <td>{d.roll}</td>
                                                                        </tr>
                                                                        <tr>
                                                                            <td>Batch:</td>
                                                                            <td>{d.batch}</td>
                                                                        </tr>
                                                                    </tbody>
                                                                </table>
                                                            </div>
                                                        </div>
                                                    </div>
                                                )
                                            })}
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="col-7-xxxl col-12">
                            <div className="card dashboard-card-eleven">
                                <div className="card-body">
                                    <div className="heading-layout1">
                                        <div className="item-title">
                                            <h3>All Expenses</h3>
                                        </div>
                                        <div className="dropdown">
                                            <a className="dropdown-toggle" href="#" role="button" data-toggle="dropdown"
                                                aria-expanded="false">...</a>

                                            <div className="dropdown-menu dropdown-menu-right">
                                                <a className="dropdown-item" href="#"><i
                                                    className="fas fa-times text-orange-red"></i>Close</a>
                                                <a className="dropdown-item" href="#"><i
                                                    className="fas fa-cogs text-dark-pastel-green"></i>Edit</a>
                                                <a className="dropdown-item" href="#"><i
                                                    className="fas fa-redo-alt text-orange-peel"></i>Refresh</a>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="table-box-wrap">
                                        {/* <form className="search-form-box">
                                            <div className="row gutters-8">
                                                <div className="col-lg-4 col-md-3 form-group">
                                                    <input type="text" placeholder="Search by Exam ..."
                                                        className="form-control" />
                                                </div>
                                                <div className="col-lg-3 col-md-3 form-group">
                                                    <input type="text" placeholder="Search by Subject ..."
                                                        className="form-control" />
                                                </div>
                                                <div className="col-lg-3 col-md-3 form-group">
                                                    <input type="text" placeholder="dd/mm/yyyy" className="form-control" />
                                                </div>
                                                <div className="col-lg-2 col-md-3 form-group">
                                                    <button type="submit"
                                                        className="fw-btn-fill btn-gradient-yellow">SEARCH</button>
                                                </div>
                                            </div>
                                        </form> */}
                                        <div className="table-responsive expenses-table-box">
                                            <table className="table data-table text-nowrap">
                                                <thead>
                                                    <tr>
                                                        <th>
                                                            <div className="form-check">
                                                                <input type="checkbox" className="form-check-input checkAll" />
                                                                <label className="form-check-label">ID</label>
                                                            </div>
                                                        </th>
                                                        <th>Expanse</th>
                                                        <th>Amount</th>
                                                        <th>Status</th>
                                                        <th>E-Mail</th>
                                                        <th>Date</th>
                                                        <th></th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        <td>
                                                            <div className="form-check">
                                                                <input type="checkbox" className="form-check-input" />
                                                                <label className="form-check-label">#0021</label>
                                                            </div>
                                                        </td>
                                                        <td>Exam Fees</td>
                                                        <td>Rs. 500</td>
                                                        <td className="badge badge-pill badge-success d-block mg-t-8">Paid</td>
                                                        <td>exampleschool@gmail.com</td>
                                                        <td>22/02/2019</td>
                                                    </tr>
                                                    <tr>
                                                        <td>
                                                            <div className="form-check">
                                                                <input type="checkbox" className="form-check-input" />
                                                                <label className="form-check-label">#0022</label>
                                                            </div>
                                                        </td>
                                                        <td>Semister Fees</td>
                                                        <td>Rs. 10000</td>
                                                        <td style={{ cursor: "pointer" }} onClick={handlePayClick} data-toggle="modal" data-target="#myModal" className="badge badge-pill badge-danger d-block mg-t-8">Due</td>
                                                        <td>exampleschool@gmail.com</td>
                                                        <td>22/02/2019</td>
                                                    </tr>
                                                    <tr>
                                                        <td>
                                                            <div className="form-check">
                                                                <input type="checkbox" className="form-check-input" />
                                                                <label className="form-check-label">#0023</label>
                                                            </div>
                                                        </td>
                                                        <td>Exam Fees</td>
                                                        <td>Rs. 500</td>
                                                        <td className="badge badge-pill badge-success d-block mg-t-8">Paid</td>
                                                        <td>exampleschool@gmail.com</td>
                                                        <td>22/02/2019</td>
                                                    </tr>
                                                </tbody>
                                            </table>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
            <div className="modal fade" id="myModal" tabIndex="-1" role="dialog" aria-labelledby="myModalLabel" aria-hidden="true">
                <div className="modal-dialog" role="document">
                    <div className="modal-content">
                        <div className="modal-header">
                            <h5 className="modal-title" id="myModalLabel">Payment</h5>
                            <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                                <span aria-hidden="true">&times;</span>
                            </button>
                        </div>
                        <div className="modal-body">
                            <label>Due Amount: Rs.10000</label>
                            <div className="new-added-form">
                                <div className="row">
                                    <div className="col-xl-12 col-lg-6 col-12 form-group">
                                        <label>Card Number *</label>
                                        <input name="cardNumber" onChange={handleInputChange} value={inputs.cardNumber} type="text" placeholder="XXXX XXXX XXXX XXXX" className="form-control" />
                                    </div>
                                </div>
                                <div className="row">
                                    <div className="col-xl-6 col-lg-6 col-12 form-group">
                                        <label>CVV *</label>
                                        <input name="cvv" onChange={handleInputChange} value={inputs.cvv} type="text" placeholder="XXX" className="form-control" />
                                    </div>
                                    <div className="col-xl-6 col-lg-6 col-12 form-group">
                                        <label>Expiration Date *</label>
                                        <input name="expDate" onChange={handleInputChange} value={inputs.expDate} type="text" placeholder="MM/YY" className="form-control" />
                                    </div>
                                </div>
                            </div>
                        </div>
                        <div className="modal-footer">
                            <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                            <button type="button" className="btn btn-primary">Pay</button>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default withRouter(Parents);