import React from 'react';
import LoginForm from "../components/Login/Login";
import NavBar from "../components/NavBar/NavBar";
import Home from "../components/home/home";
import StudentUpload from "../components/StudentUpload/StudentUpload";
import Parents from "../components/Parents/Parents";
import CentralAdmin from "../components/CentralAdmin/CentralAdmin";
import StudentDetails from "../components/StudentDetails/StudentDetails";



const routes = [
	{
		path: '/',
		component: LoginForm,
		isPrivate: false,
	},
	{
		path: '/home',
		component: Home,
		isPrivate: true,
	},
	{
		path: '/upload',
		component: StudentUpload,
		isPrivate: true,
	},
	{
		path: '/parents',
		component: Parents,
		isPrivate: true,
	},
	{
		path: '/admin',
		component: CentralAdmin,
		isPrivate: true,
	},
	{
		path: '/student-details',
		component: StudentDetails,
		isPrivate: true,
	},
	// {
	// 	path: '/*',
	// 	component: NotFound,
	// 	isPrivate: true,
	// },
];

export default routes;
