import React, { useState, useContext,useEffect } from "react";
import readXlsxFile from 'read-excel-file'
import { withRouter } from 'react-router-dom';
import './CentralAdmin.css';
import Header from '../Header/Header';
import Sidebar from '../Sidebar/Sidebar';
import Modal from '../PaymentModal/PaymentModal';
import ReactFlexyTable from "react-flexy-table";
import "react-flexy-table/dist/index.css";
import ExpensesBarChar from '../AdminDashboard/ExpensesBarChar';
import StudentsPieChart from '../AdminDashboard/StudentsPieChart';
import EarningLineChart from '../AdminDashboard/EarningLineChart';
import Cards from '../AdminDashboard/Cards';
import { Button } from "react-bootstrap";

const CentralAdmin = (props) => {
    // const [showDialog, setShowDialog] = useState(false)
    const [studArr, setStudArr] = useState([])
    const additionalCols = [
        {
            header: "Action",
            td: (data) => {
                if (data.fees >= 30000) {
                    return (
                        <div>
                            <Button
                                className="paidBtn"
                                onClick={() => alert("Full Fees Paid Rs. " + data.fees)}
                            >
                                Paid
                  </Button>
                        </div>
                    );
                } else if (data.fees > 15000 && data.fees < 30000) {
                    return (
                        <div>
                            <Button
                                className="pendingBtn1"
                                onClick={() =>
                                    alert("Pending  Fees Rs. " + (30000 - data.fees))
                                }
                            >
                                Pending
                  </Button>
                        </div>
                    );
                } else {
                    return (
                        <div>
                            <Button
                                className="pendingBtn"
                                onClick={() =>
                                    alert("Pending  Fees Rs. " + (30000 - data.fees))
                                }
                            >
                                Pending
                  </Button>
                        </div>
                    );
                }
            },
        },
    ];

    useEffect(() => {
        const requestOptions = {
            method: 'GET',
            headers: {
                'Content-Type': 'application/json',
                "Access-Control-Allow-Origin": "*"
            }
        };
        fetch('https://asychrondemonew.herokuapp.com/api/students', requestOptions)
            .then(response => response.json())
            .then(data => {
                console.log("Get stud resp: ", data);
                setStudArr(data);
            });

    }, []);

    return (
        <div>
            <Header />
            <div className="dashboard-page-one">
                <Sidebar />
                <div className="dashboard-content-one">
                    <div className="homeWrap"></div>
                    <Cards/>
                    <ExpensesBarChar/>
                    <StudentsPieChart/>
                    <EarningLineChart/>
                    <ReactFlexyTable
                        data={studArr}
                        sortable
                        filterable
                        globalSearch
                        additionalCols={additionalCols}
                        pageSizeOptions={[10, 20, 50, 80]}
                        className="my-table"
                    />
                </div>
            </div>
        </div>
    )
};

export default withRouter(CentralAdmin);