import React,{useState} from "react";
import './Login.css';

const LoginForm = (props) => {
    const [inputs, setInputs] = useState(
        {
            "username": "",
            "password": "",
        });
    const handleTodoSubmit = (event) => {
        event.preventDefault();
        console.log("Login pass: ",inputs);
        localStorage.setItem("utype",inputs.username);
        if(inputs.username === "admin"){
            props.history.push('/home');
        }
        else if(inputs.username === "parent"){
            props.history.push('/parents');
        }
        else if(inputs.username === "cadmin"){
            props.history.push('/admin');
        }
    };
    const handleInputChange = (event) => {
        setInputs(inputs => ({ ...inputs, [event.target.name]: event.target.value }));
    }

    return (
        <div className="wrapper1 fadeInDown">
            <div className="formContent">
                <div className="card height-auto">
                    <div className="card-body">
                        <div className="heading-layout1">
                            <div className="item-title">
                                <h3>Login</h3>
                            </div>
                        </div>
                        <form className="new-added-form">
                            <div className="column">
                                <div className="col-lg-12 col-12 form-group">
                                    {/* <label>Username *</label> */}
                                    <input name="username" onChange={handleInputChange} value={inputs.username} type="text" placeholder="Username" className="form-control" />
                                </div>
                                <div className="col-lg-12 col-12 form-group">
                                    {/* <label>Password *</label> */}
                                    <input name="password" onChange={handleInputChange} value={inputs.password} type="password" placeholder="Password" className="form-control" />
                                </div>
                                <div className="col-12 form-group mg-t-8">
                                    <button onClick={handleTodoSubmit} type="button" className="btn-fill-lg bg-blue-dark btn-hover-yellow">Login</button>
                                </div>
                            </div>
                        </form>
                    </div>
                </div>
            </div>
        </div>
    )
};

export default LoginForm;