import React from 'react';
import {Bar} from 'react-chartjs-2';

const state = {
  labels: ['January', 'February', 'March',
           'April', 'May'],
  datasets: [
    {
      label: 'Expenses',
      backgroundColor: 'rgba(75,192,192,1)',
      borderColor: 'rgba(0,0,0,1)',
      borderWidth: 0,
      data: [125000, 25000, 55000, 85000, 35000]
    }
  ]
}



export default class ExpensesBarChar extends React.Component {
  render() {
    return (
        <div>
             <Bar
          data={state}
          // width={50}
          // height={50}
          options={{
            title:{
              display:true,
              text:'Expenses',
              fontSize:20
            },
            scales: {
                xAxes: [{
                    gridLines: {
                        display:false
                    }
                }],
                yAxes: [{
                    gridLines: {
                        display:true
                    }, ticks: {
                        callback: function(label, index, labels) {
                            return label/1000+'k';
                        },
                        beginAtZero: true,
            min: 0
                    },
                    scaleLabel: {
                        display: false,
                        labelString: '1k = 1000'
                    }   
                }]
            },
            legend:{
              display:true,
              position:'bottom',
              labels: {
                fontSize:14,
                // fontColor:'red'
            }
              
            }
          }}
        
        />
      </div>
      
    );
  }
}